import React, {useState} from 'react'



export default function Forms(props) {
  

  const handleUpClick = () => {
    console.log("Uppercase was clicked")
    let newtext = text.toUpperCase()
    setText(newtext)
  }

  const handleLoClick = () => {
    console.log("Lowercase was clicked")
    let newtext = text.toLowerCase()
    setText(newtext)
  }

  const handleOnChange = (event) => {
    console.log("Onchange")
    setText(event.target.value)
  }

  const [text, setText] = useState("");


  return (
      <>
      <div className="container" style={{color: props.Mode==='dark'?'white':'black'}}>
      <h1>{props.heading}</h1>
      <div className="mb-3">
      <textarea type="text" className="form-control" id="mybox" style={{backgroundColor:props.Mode==='dark'?'grey':'white', color: props.Mode==='dark'?'white':'black'}} value={text} onChange={handleOnChange}
        rows="8"></textarea>
      </div>
      <button className="btn btn-success mx-2" onClick={handleUpClick}>Convert to uppercase</button>
      <button className="btn btn-success mx-2" onClick={handleLoClick}>Convert to Lowercase</button>
      </div>

      <div className='container my-3' style={{color: props.Mode==='dark'?'white':'#151536'}}>
        <h2>Your Text Summary</h2>
        <p>{text.split(" ").length} words and {text.length} characters</p>
        <p>{0.008*text.split(" ").length} minutes </p>
        <h2>Preview</h2>
        <p>{text.length>0?text:"*Enter something to preview it here."}</p>
        </div>
      </>
  )
}










